* {
  margin: 0px;
  padding: 0px;
}

.navbar {
  display: flex;
  /* margin-top: 40px; */
  /* margin-left: 122px; */
  justify-content: center;
  align-items: center;
  /* border: 4px solid rgb(114, 108, 108); */
  position: sticky;
  top: 0;
  height: 110px;
  background-color: white;
}
.navbar ul {
  display: flex;
  justify-content: end;
  align-items: center;
  /* border: 4px solid black; */
  width: 100%;
}

.navbar ul li {
  list-style: none;
  /* padding: 4px; */
}
.navbar ul li a {
  text-decoration: none;
  list-style: none;
  margin: 16px;
  color: black;
  /* margin-top: 8px; */
  font-size: 20px;
  font-family: "ZCOOL XiaoWei", serif;
  /* padding: 4px; */
  position: relative;
  /* border: 2px solid rgb(211, 20, 169); */
}
.navbar ul li a:after {
  content: "";
  position: absolute;
  background-color: #ce7011;
  height: 3px;
  width: 0;
  left: 0;
  bottom: -7px;
  transition: 0.3s;
}
.navbar a:hover:after {
  width: 100%;
}
.navbar ul li a:hover {
  background: #f4ece6;
  color: black;
  /* border: 2px solid blue; */
  border-radius: 5px;
  border: 1px solid black;
}
.nav_phone {
  display: none;
}
#fixed_underline::after {
  content: "";
  position: absolute;
  height: 3px;
  width: 100%;
  background-color: red;
  left: 0;
  bottom: -7px;
  animation: underline_appear 1s linear;
}
@keyframes underline_appear {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
