  padding-top: 2px;
  padding-bottom: 2px;
  margin-bottom: 10px;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  border: 1px dashed rgb(24, 21, 21);
}
.loginformofAdmin button:hover{
 background-color: #90967a;
 cursor: pointer;
 border: 1px solid rgb(24, 21, 21);
}
