/**
      *!media query for NAVBAR section
      **/

@media only screen and (max-width: 759px) {
  .line-1 {
    width: 30px;
    height: 3px;
    background-color: blue;
    margin: 5px;
  }
  .line-2 {
    width: 20px;
    height: 3px;
    background-color: blue;
    margin: 5px;
  }
  .line-3 {
    width: 10px;
    height: 3px;
    background-color: blue;
    margin: 5px;
  }
  .nav_phone {
    position: absolute;
    right: 30px;
    top: 50%;
    cursor: pointer;
    z-index: 2;
    display: block;
    cursor: pointer;
    /* border: 4px solid red; */
  }
  .navmenu {
    position: fixed;
    bottom: -100%;
    height: 100%;
    bottom: 0;
    opacity: 0;

    flex-direction: column;
    background-color: #f4ece6;
    width: 100%;

    text-align: center;
    transition: 0.3s;
  }
  .navbar ul {
    justify-content: start;
    width: 0;
    height: 0px;
    margin-top: 0;
    opacity: 2;
  }
  .navbar ul li {
    margin: 20px;
    /* opacity: 0; */
  }
  .navmenu.active_nav_phone {
    top: 88px;
    bottom: 0;
    width: 100%;
    height: 100%;
    right: -18px;
    /* border: 8px solid yellow; */
    border-radius: 15px;
    opacity: 2;
    left: 0px;
    color: blue;
  }
  .y {
    opacity: 0;
    height: 0;
    width: 0;
  }

  .animation_nav_phone_lines .line-1 {
    width: 25px;
    transform: rotate(-45deg) translate(-5px, 6px);
    background-color: #000000;
  }
  .animation_nav_phone_lines .line-2 {
    opacity: 0;
  }
  .animation_nav_phone_lines .line-3 {
    width: 25px;
    transform: rotate(45deg) translate(-5px, -6px);
    background-color: #000000;
  }
  .navbar ul li a {
    color: blue;
    font-size: 32px;
  }
  #x_off #off {
    opacity: 0;
  }
}

